import React, { useState } from "react";
import styles from "./Donate.module.css";
import vip from "./../../assets/images/donate/vip.png";
import premium from "./../../assets/images/donate/premium.png";
import master from "./../../assets/images/donate/master.png";
import chaotic from "./../../assets/images/donate/chaotic.png";
import { Popup } from "../Popup/Popup";

export const Donate = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [contentVip, setVip] = useState(
    <>
      <li
        style={{
          fontSize: "40px",
          textAlign: "center",
          margin: "0 auto",
        }}
      >
        Особенности донат услуги
        <li className={styles.closeIcon} onClick={() => closePopup()}>
          &times;
        </li>
      </li>
      <hr
        style={{
          border: "0.5px solid",
          borderRadius: "10px",
          width: "1000px",
          height: "1px",
          backgroundColor: "white",
        }}
      />

      <li>
        150 тыс. блоков <br />
        Максимальный размер региона, который Вы можете заприватить
      </li>
      <br />
      <li>
        4 региона
        <br /> Максимальное число приватов, которые Вы можете создать
      </li>
      <br />
      <li>
        Набор флагов VIP <br />
        Creeper-explosion, ghast-fireball, snow-fall, ice-form, ice-melt и
        leaf-decay
      </li>
      <li>/tpahere Телепортировать игрока к себе</li>
      <br />
      <li>/back Вернуться на точку смерти</li>
      <br />
      <li>/fly Включить режим полёта</li>
      <br />

      <li>
        Резервный слот <br />
        Вход на заполенный сервер
      </li>
      <br />
      <li>
        <br />
        /near Вывести список всех игроков в радиусе 200 блоков
      </li>
      <br />
      <li>
        /repair all Починить все инструменты (не из модов) в Вашем инвентаре
      </li>
      <br />
      <li>/heal Полностью восстановить Вашу шкалу здоровья</li>
      <br />
      <li>/feed Полностью восстановить Вашу шкалу голода</li>
      <br />
      <li>
        Сохранение инвентаря <br />
        При смерти Ваш инвентарь полностью сохраняется, но из-за многочисленных
        багов игры функция с маленьким шансом может не сработать.
      </li>
      <br />
      <li>Смена плаща Доступ к установке и смене плаща в личном кабинете</li>
      <br />
      <li>/firework Настроить эффект у фейерверка</li>
      <br />
    </>
  );
  const [contentPremium, setPremium] = useState(
    <>
      <li
        style={{
          fontSize: "40px",
          textAlign: "center",
          margin: "0 auto",
        }}
      >
        Особенности донат услуги
        <li className={styles.closeIcon} onClick={() => closePopup()}>
          &times;
        </li>
      </li>
      <hr
        style={{
          border: "0.5px solid",
          borderRadius: "10px",
          width: "1000px",
          height: "1px",
          backgroundColor: "white",
        }}
      />

      <li>
        150 тыс. блоков <br />
        Максимальный размер региона, который Вы можете заприватить
      </li>
      <br />
      <li>
        6 регионов <br />
        Максимальное число приватов, которые Вы можете создать
      </li>
      <br />
      <li>
        Флаги <br />
        Premium mob-spawning, mob-damage, lava-fire, deny-spawn и все флаги
        группы VIP
      </li>
      <br />
      <li>
        /hat Надеть на голову тот блок или предмет, который Вы держите в руке
      </li>
      <br />
      <li>Приватный варп Доступна 1 приватная точка телепортирования (warp)</li>
      <br />
      <li>Оформление в чате Можно сменить цвет ника в личном кабинете</li>
      <br />
      <li>Смена плаща HD Можно установить плащ высокого качества</li>
      <br />
      <li>Смена скина HD Можно установить скин высокого качества</li>
      <br />
      <li>/god Включить режим бессмертия</li>
      <br />
      <li>/ptime Изменить время суток только для Вас</li>
      <br />
      <li>/enderchest Открыть портативный сундук края</li>
      <br />
      <li>/clear Очистить свой инвентарь</li>
      <br />
      <li>
        /legend Узнать ник игрока, рядом с которым находится легендарный покемон
      </li>
      <br />
      <li>Цвет на табличках Можно писать разными цветами на табличках</li>
      <br />
      <li>/craft Открыть портативный верстак</li>
      <br />
    </>
  );
  const [contentMaster, setMaster] = useState(
    <>
      <li
        style={{
          fontSize: "40px",
          textAlign: "center",
          margin: "0 auto",
        }}
      >
        Особенности донат услуги
        <li className={styles.closeIcon} onClick={() => closePopup()}>
          &times;
        </li>
      </li>
      <hr
        style={{
          border: "0.5px solid",
          borderRadius: "10px",
          width: "1000px",
          height: "1px",
          backgroundColor: "white",
        }}
      />

      <li>
        200 тыс. блоков <br />
        Максимальный размер региона, который Вы можете заприватить
      </li>
      <li>
        8 регионов <br />
        Максимальное число приватов, которые Вы можете создать
      </li>
      <li>
        Флаги <br />
        Master invincible, feed-min-hunger, feed-max-hunger, water-flow,
        lava-flow, entry и все флаги групп VIP и PREMIUM
      </li>
      <li>/getpos Получить данные о Вашем местоположении</li>
      <li>
        Стол раскрафта
        <br /> Доступ к использования стола раскрафта без ограничений
      </li>
      <li>
        Сохранение опыта <br />
        После смерти весь опыт и уровень сохраняется
      </li>
      <li>
        Приватные варпы Доступны 2 приватные точки телепортирования (warp)
      </li>
      <li>
        Публичный варп Можно поставить 1 публичный варп, доступный всем игрокам
      </li>
      <br />
      <li>/jumpto Прыгнуть туда, куда Вы смотрите</li>
      <li>/compass Открыть электронный компас</li>
      <li>/ext Мгновенно потушить себя</li>
      <li>/ext [игрок] Потушить другого игрока</li>
      <li>
        Повышение уровня здоровья Пассивный эффект: появляется вторая полоска
        здоровья, размер сердец в которой увеличивается от количества опыта до
        30 уровня
      </li>
      <li>/heal [игрок] Вылечить другого игрока</li>
      <li>Цвет в чате Можно писать в чат разным цветом</li>
      <li>
        Оформление в чате Возможность менять цвет префикса и цвет ника в личном
        кабинете, а также устанавливать собственный уникальный префикс!
      </li>
    </>
  );
  const [contentChaotic, setChaotic] = useState(
    <>
      <li
        style={{
          fontSize: "40px",
          textAlign: "center",
          margin: "0 auto",
        }}
      >
        Особенности донат услуги
        <li className={styles.closeIcon} onClick={() => closePopup()}>
          &times;
        </li>
      </li>
      <hr
        style={{
          border: "0.5px solid",
          borderRadius: "10px",
          width: "1000px",
          height: "1px",
          backgroundColor: "white",
        }}
      />

      <li>
        600 тыс. блоков <br />
        Максимальный размер региона, который Вы можете заприватить
      </li>
      <li>
        10 регионов
        <br /> Максимальное число приватов, которые Вы можете создать
      </li>
      <li>
        Флаги Legend
        <br /> Все возможные флаги групп Vip, Premium и Master
      </li>
      <li>/heal Мгновенное использование команды без задержек</li>
      <li>
        Приватные варпы
        <br /> Доступно 5 приватных точек телепортирования (warp)
      </li>
      <li>
        Публичные варпы
        <br /> Можно поставить 3 публичных варпа, доступных всем игрокам
      </li>
      <li>/depth Узнать Ваше местоположение относительно уровня моря</li>
      <li>/feed [игрок] Накормить другого игрока</li>
      <li>/top Телепортироваться наверх</li>
      <li>/world Телепортация между измерениями</li>
      <li>/pweather Изменить погоду только для Вас</li>
      <li>/speed [1-10] Установить скорость полета</li>
      <li>
        Оформление в чате
        <br /> Доступна полная кастомизация цвета ника и сообщения, префикса и
        суффикса в личном кабинете, а также можно поставить свой уникальный
        префикс!
      </li>
      <li>
        Цветные буквы в ЛС <br />
        Можно писать в ЛС разным цветом
      </li>
    </>
  );
  const [content, setContent] = useState("");

  const closePopup = () => {
    setIsOpen(isOpen);
  };

  const togglePopup = (donateType) => {
    switch (donateType) {
      case "vip": {
        setIsOpen(!isOpen);
        setVip(contentVip);
        setContent(contentVip);
        break;
      }
      case "premium": {
        setIsOpen(!isOpen);
        setPremium(contentPremium);
        setContent(contentPremium);
        break;
      }
      case "master": {
        setIsOpen(!isOpen);
        setMaster(contentMaster);
        setContent(contentMaster);
        break;
      }
      case "chaotic": {
        setIsOpen(!isOpen);
        setChaotic(contentChaotic);
        setContent(contentChaotic);
        break;
      }
      default: {
        break;
      }
    }
  };

  return (
    <div className={styles.container}>
      <ul className={styles.header}>
        <li>
          <h1>Донат услуги</h1>
        </li>
      </ul>
      <ul className={styles.ulContainer}>
        <li>
          <img
            src={vip}
            alt=""
            style={{ width: "300px" }}
            onClick={() => togglePopup("vip")}
          />
        </li>
        <li>
          <img
            src={premium}
            alt=""
            style={{ width: "300px" }}
            onClick={() => togglePopup("premium")}
          />
        </li>
        <li>
          <img
            src={master}
            alt=""
            style={{ width: "300px" }}
            onClick={() => togglePopup("master")}
          />
        </li>
        <li>
          <img
            src={chaotic}
            alt=""
            style={{ width: "300px" }}
            onClick={() => togglePopup("chaotic")}
          />
        </li>
        <div>
          {isOpen && (
            <Popup
              className={styles.popup}
              content={content}
              handleClose={closePopup}
            />
          )}
        </div>
      </ul>
    </div>
  );
};
