import React from "react";
import styles from "./Home.module.css";
import newsBg from "./../../assets/images/newsBackground.jpg";
import newsBg2 from "./../../assets/images/newsBackground2.jpg";
import { NavLink } from "react-router-dom";

export const Home = () => {
  return (
    <div className={styles.rulesContainer}>
      <div className={styles.rulesContainerItem}>
        <div className={styles.aboutUs}>
          Что такое UpCraft?<hr style={{border: "1px solid", borderRadius: "30px"}} />
          <div className={styles.aboutUsBlock}>
            UpCraft - это проект с модами популярной игры Minecraft. <br />У нас
            есть 4 сервера, которые подойдут для игры каждому. <br />
            На каждом сервере присутствует особая концепция игры - прокачка
            персонажа и поэтапное открытие крафтов. <br /> Каждый новый игрок
            начинает с первого уровня, имея при себе несколько предметов в
            инвентаре, добывая ресурсы и открывая крафты, постепенно повышая
            уровень. <br />
            Вам предстоит дойти до последнего - сотого уровня. <br /> Полный
            вайп серверов происходит раз в 4 месяца.
          </div>
        </div>
       
        <div className={styles.news}>
          Новости<hr style={{border: "1px solid", borderRadius: "30px"}} />
          <div className={styles.newsBlock}>
            <div className={styles.bg}>
              <img src={newsBg} alt="" />
            </div>
            <div className={styles.newsText}>
              10.05.2022 <br />
              Все сервера успешно обновлены
            </div>
          </div>
          <div className={styles.newsBlock}>
            <div className={styles.bg}>
              <img src={newsBg2} alt="" />
            </div>
            <div className={styles.newsText}>
              03.05.2022 <br />
              Скидки 50% на донат
            </div>
          </div>
          
        </div>
        <div className={styles.play}>
          <NavLink to={"/download"}>НАЧАТЬ ИГРАТЬ</NavLink>
        </div>
      </div>
    </div>
  );
};
