import React from "react";
import { Route } from "react-router-dom";
import { Switch } from "react-router-dom";
import { Header } from "./components/Header/Header";
import style from "./App.module.css";
import img from "./assets/images/background.jpg";
import { Rules } from "./components/Rules/Rules";
import { Download } from "./components/Download/Download";
import { Servers } from "./components/Servers/Servers";
import { Home } from "./components/Home/Home";
import { Footer } from "./components/Footer/Footer";
import { Donate } from "./components/Donate/Donate";
import { HashRouter } from "react-router-dom";

class App extends React.Component {
  render() {
    const styles = {
      container: {
        backgroundImage: `url(${img})`,
        backgroundRepeat: "noRepeat",
        position: "fixed",
        backgroundSize: "cover",
        width: "100vw",
        height: "100vh",
      },      
    };

    return (
      <div style={styles.container}>
        <div className={style.bg}>
          <div className={style.appWrapper} >
            <Switch>
              <Route exact path="/" render={() => <Home />} />
              <Route exact path="/UpCraft" render={() => <Home />} />
              <Route path="/rules" render={() => <Rules />} />
              <Route path="/download" render={() => <Download />} />
              <Route path="/servers" render={() => <Servers />} />
              <Route path="/donate" render={() => <Donate />} />
            </Switch>
          </div>
          <div className={style.mainDiv}>
            <Header />
          </div>
          {/* <img src={img} className={style.image} /> */}
        </div>
        <Footer />
      </div>
    );
  }
}

const ReactUpCraft = (props) => {
  return (
    <HashRouter basename={process.env.PUBLIC_URL}>
      <App />
    </HashRouter>
  );
};

export default ReactUpCraft;
