import React from "react";
import styles from "./Download.module.css";
import windows from "./../../assets/images/windows.svg";
import linux from "./../../assets/images/linux.svg";
import { saveAs } from "file-saver";

export const Download = () => {
  const winDownload = () => {
    saveAs(
      "https://launcher.streamcraft.net/cdn/program/StreamCraft.exe",
      "UpCraft.exe"
    );
  };

  const linuxDownload = () => {
    saveAs(
      "https://launcher.streamcraft.net/cdn/program/StreamCraft.jar",
      "UpCraft.jar"
    );
  };

  return (
    <div className={styles.rulesContainer}>
      <ul className={styles.rulesContainerHeader}>
        <li>
          <h1>Скачать лаунчер</h1>
        </li>
      </ul>
      <ul className={styles.rulesContainerItem}>
        <li>
          <img
            src={windows}
            alt=""
            style={{ width: "300px" }}
            onClick={winDownload}
          />
        </li>
        <li>
          <img
            src={linux}
            alt=""
            style={{ width: "300px" }}
            onClick={linuxDownload}
          />
        </li>
      </ul>
    </div>
  );
};
