import React, { useState } from "react";
import styles from "./Servers.module.css";
import upClassic from "./../../assets/images/UpClassic.jpg";
import upMagic from "./../../assets/images/UpMagic.jpg";
import upRpg from "./../../assets/images/UpRpg.jpg";
import UpMagicRpg from "./../../assets/images/UpMagicRpg.jpg";

const data = [
  {
    id: "1",
    key: "1",
    title: "UpClassic",
    text: "1.16.5",
    img: upClassic,
  },
  {
    id: "2",
    key: "2",
    title: "UpMagic",
    text: "1.12.2",
    img: upMagic,
  },
  {
    id: "3",
    key: "3",
    title: "UpRpg",
    text: "1.12.2",
    img: upRpg,
  },
  {
    id: "4",
    key: "4",
    title: "UpMagicRpg",
    text: "1.12.2",
    img: UpMagicRpg,
  },
];

export const Servers = () => {
  const [toggle, setToggle] = useState("1");

  return (
    <div className={styles.rulesContainer}>
      <div className={styles.rulesContainerHeader}>
        <h1>Наши сервера</h1>
        {data.map(({ title, text, key, img }) => {
          return (
            <>
              <div className={styles.main}>
                <div className={styles.img}>
                  {toggle === key ? (
                    <div>
                      <img src={img} key={key} className={styles.photo} alt=""/>
                    </div>
                  ) : null}
                </div>
                <div className={styles.text}>
                  <div className={styles.serverContainer}>
                    <h1 onClick={() => setToggle(key)}>{title} </h1>
                    {toggle === key ? (
                      <div>
                        <p>Версия игры {text}</p>
                      </div>
                    ) : null}
                  </div>
                </div>
              </div>
            </>
          );
        })}
      </div>

      {/* <ul className={styles.rulesContainerHeader}>
        <li>
          <h1>Наши сервера</h1>
        </li>
      </ul>
      <ul className={styles.rulesContainerItem}>
        <li className={styles.rulesContainerItemLi}>
          <h1>
            <img alt="" />
            UpClassic
            <button className={styles.sliderBtn}>&lt;</button>
            <button className={styles.sliderBtn}>&gt;</button>
          </h1>
        </li>
      </ul> */}
    </div>
  );
};
